import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-page2',
  templateUrl: './page2.component.html',
  styleUrls: ['./page2.component.css']
})
export class Page2Component implements OnInit {

  constructor() { }

  ngOnInit(): void {   }


  public show:boolean=true ;
  public show2:boolean=false ;
  public show3:boolean=false ;
  public show4:boolean=false ;
  public buttonName:any = 'Show';
  public buttonName2:any = 'Show 2';
  public buttonName3:any = 'Show 2';
  public buttonName4:any = 'Show 2';

  toggle() {
    if(this.show=true){ 
      this.show2=false;
      this.show3=false;
      this.show4=false; 
     }


     
  }



  toggle2() {
    if(this.show2=true) {
    
     this.show=false;
     this.show3=false;
     this.show4=false; 
    }
  }

  
  toggle3() {
    if(this.show3=true) {
     this.show=false;
     this.show2=false;
     this.show4=false;  
    }
  }


  toggle4() {
    if(this.show4=true) {
     this.show=false;
     this.show2=false; 
     this.show3=false; 
    }
  }

}
